* {
    margin: 0;
    padding: 0;
}

html, body, #root {
    background-image: url("./img/tile.png");
    background-repeat: repeat;
    background-size: 3vmax;

    height: 100%;

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

#body {
    padding-top: 3vmax;
}

#SuccessfulPurchase #footer {
    position: fixed;
    bottom: 0
}



